import {
	Grid,
	Button,
	Stack,
	Divider,
	CardContent,
	CardActions,
	CircularProgress,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useEffect } from 'react';
import MainCard from '../../../ui-component/cards/MainCard';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import SubCard from '../../../ui-component/cards/SubCard';
import { gridSpacing } from '../../../store/constant';
import { TOffer, TUploadFile } from '../../../types';
import graphqlSchema from '../../../graphql';
import { convertToFile } from '../../../helpers';
import UploadFile from '../components/UploadFile';
import OfferActions from './OfferActions';
import { FILE_CONFIG } from './config';
import { useUploadFiles } from '../../../hooks';
import config from '../../../config';
import DownloadButton from './components/DownloadButton';
import ExportButton from './components/ExportButton';
import Contingency from './components/Contingency';
import OfferInformation from './components/OfferInformation';
import BuyerInformation from './components/BuyerInformation';
import SellerInformation from './components/SellerInformation';

type Props = {
	offer: TOffer;
	refetch: () => void;
};

const DOCUMENT_TYPE = {
	PA: 'SignedPurchaseAgreement',
	SCAR: 'SCAR',
};

const OfferEdit = ({ offer, refetch }: Props) => {
	const offerId = _get(offer, 'OfferId', '');
	const currentStepId = +_get(offer, 'StepId', 0);
	const offerNo = _get(offer, 'OfferNo', '');
	const status = _get(offer, 'Step.messages.advisor', '');
	const title = `${offerNo}: ${status}`;
	const submissionFiles = _get(offer, 'SubmissionFiles', []);
	const scarReport = _get(offer, ['SCARReport', 'PresignedUrl'], []);
	const scarReportFile = { url: scarReport, label: 'Export Request for Repair' };

	const generateOptions = (documentType = DOCUMENT_TYPE.SCAR) => ({
		name: 'getOfferDocPutUrls',
		query: graphqlSchema.getOfferDocPutUrls,
		generateVariables: (files: TUploadFile[]) => ({
			Documents: files,
			OfferId: offerId,
			DocumentType: documentType,
		}),
		refetch,
	});

	const {
		files: PAFiles,
		onFilesChange: onPAChange,
		uploadFiles: uploadPA,
		isUploadLoading: isPAUploading,
	} = useUploadFiles(generateOptions(DOCUMENT_TYPE.PA));

	const {
		files: SCARFiles,
		onFilesChange: onSCARChange,
		uploadFiles: uploadSCAR,
		isUploadLoading: isSCARUploading,
	} = useUploadFiles(generateOptions());

	useEffect(() => {
		const docPA = _get(offer, 'SignedPurchaseAgreement') || [];
		const docSCAR = _get(offer, 'SCAR') || [];

		if (!_isEmpty(docPA)) onPAChange(docPA.map(convertToFile));
		if (!_isEmpty(docSCAR)) onSCARChange(docSCAR.map(convertToFile));
	}, [offer]);

	const onUpdate = () => {
		if (isShowPA || isShowSCAR) {
			uploadPA();
			uploadSCAR();
		}
	};

	const isShowPA = currentStepId >= config.offerSteps.paBeingCreated;
	const isEnablePA = currentStepId === config.offerSteps.paBeingCreated;
	const isShowSCAR = currentStepId >= config.offerSteps.reviewScarRequest;
	const isEnableSCAR = currentStepId === config.offerSteps.reviewScarRequest;
	const isUploading = isPAUploading || isSCARUploading;

	return (
		<MainCard
			title={title}
			content={false}
			secondary={<OfferActions offer={offer} />}
		>
			<CardContent>
				<Grid container spacing={gridSpacing}>
					<Grid item lg={6} xs={12}>
						<Grid container direction='column'>
							<Grid item xs={12}>
								<Contingency offer={offer} />
							</Grid>
							<Grid item xs={12}>
								<OfferInformation offer={offer} />
							</Grid>
						</Grid>
					</Grid>
					<Grid item lg={6} xs={12}>
						<Grid container direction='column' spacing={gridSpacing}>
							<Grid item xs={12}>
								<BuyerInformation offer={offer} />
							</Grid>
							<Grid item xs={12}>
								<SellerInformation offer={offer} />
							</Grid>
							{isShowPA && (
								<Grid item xs={12}>
									<SubCard title='Signed Purchase Agreement'>
										<UploadFile
											name='PA'
											files={PAFiles}
											onDrop={onPAChange}
											accept={FILE_CONFIG.type}
											disabled={!isEnablePA}
										/>
									</SubCard>
								</Grid>
							)}
							{isShowSCAR && (
								<Grid item xs={12}>
									<SubCard title='Request for Repair'>
										<UploadFile
											name='SCAR'
											files={SCARFiles}
											onDrop={onSCARChange}
											accept={FILE_CONFIG.type}
											disabled={!isEnableSCAR}
										/>
									</SubCard>
								</Grid>
							)}
						</Grid>
					</Grid>
				</Grid>
			</CardContent>
			<Divider />
			<CardActions>
				<Grid item xs={12}>
					<Stack direction='row' justifyContent='flex-end'>
						<AnimateButton>
							<ExportButton file={scarReportFile} />
							<DownloadButton submissionFiles={submissionFiles} />
							<Button
								color='primary'
								variant='contained'
								type='submit'
								disabled={(!isShowPA && !isShowSCAR) || isUploading}
								onClick={onUpdate}
							>
								{isUploading ? (
									<CircularProgress size='1.5rem' color='secondary' />
								) : (
									'Update'
								)}
							</Button>
						</AnimateButton>
					</Stack>
				</Grid>
			</CardActions>
		</MainCard>
	);
};

export default OfferEdit;
